@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}


body {
  font-family: 'Poppins', sans-serif;
  color: rgba(255, 255, 255, 0.897);
  display: flex;
  /* text-shadow: 0 0 5px rgba(255, 255, 255, 0.822);  */
}

::-webkit-scrollbar{
  width: 8px;
  height: 2px;
}

::-webkit-scrollbar-track {
  border: 4px solid transparent;
  background-color: transparent;
  background-clip: content-box;
}

::-webkit-scrollbar-thumb {
  background: rgba(189, 189, 189, 0.795);
  border-radius: 5px;

}



.header {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  text-align: center;
  width: 100vw;
}

.btn {
  display: inline-block;
  background-color: rgba(255, 255, 255, 0.897);
  /* box-shadow: 0 0 10px rgb(255, 255, 255); */
  color: rgb(0, 0, 0);
  font-weight: bold;
  border: none;
  padding: 5px 5px;
  margin: 5px;
  border-radius: 10px;
  cursor: pointer;
  text-decoration: solid;
  font-size: 12px;
  font-family: inherit;
}


.screen {
  overflow: hidden;
  justify-content: center;
  align-items: center;
  margin: 0px;
  padding: 0px;
  height: 100vh;
  width: 100vw;
  background-color: black;
  background-image: url(./wp2732972.jpg);
}

