.akMystery{
  z-index: 010;
  position: relative;
  box-shadow: 0 0 60px 20px #ffffff,
  0 30px 70px 12px #ffffff;
  /* top: -125px; */
  top:-30px;
  left: 160px;
  width: 150px;
}

.akMysteryAfter{
  z-index: 1100;
  position: relative;
  box-shadow: 0 0 60px 20px #ffffff,
  0 30px 70px 12px #ffffff;
  /* top: -125px; */
  animation: SlideUp 1.2s;
  animation-fill-mode: forwards;
  top: 100px;
  left: 160px;
  width: 150px;
}

.characterMystery{
  position: relative;
  top: -125px;
  left: 175px;
  width:250px;
}

.mysteryBox{
  position: relative;
  z-index: 1050;
  top: 75px;
  right: 95px;
  height: 350px;
}

.mysteryBoxAfter{
  position: relative;
  z-index: 1050;
  top: 200px;
  right: 95px;
  height: 350px;
  animation: boxSlideDown .4s;
}

.mysteryBoxCharacter{
  position: relative;
  top: 200px;
  right: 150px;
  height: 350px;
}

.mysteryBoxSelect{
  display: flex;
  justify-content: center;
  align-items: center;
}

.mysteryButton{
  background-color: black;
  color: white;
  border-radius: 5px;
  height: 50px;
  width: 100px;
  margin: 10px;
}

@keyframes SlideUp {
  0% {
    z-index: 1010;
    top:-30px;
  }

  34%{
    top: 100px;
    z-index: 1010;
  }

  60%{
    z-index: 1100;
  }


  100% {
      z-index: 1100;
      top: -125px;
  }
}

@keyframes boxSlideDown{
  0%{
    position: relative;
    z-index: 1050;
    top: 75px;
    right: 95px;
    height: 350px;
  }

  100%{
    position: relative;
    z-index: 1050;
    top: 200px;
    right: 95px;
    height: 350px;
  }
}


