  border: 1px solid black;
  padding: 0px;
  border-radius: 5px;
}

.filter-form {
  margin-bottom: 40px;
  justify-content: space-between;
}


.notification-wrapper {
  z-index: 1500;
  position: fixed;
  top: 10px;
  right: 10px;
  width: 300px;
}

.notification-item {
  z-index: 1500;
  box-shadow: 0 0 10px rgba(255,255,255, 0.8);
  background-color: rgba(255, 255, 255, 0.904);
  text-align: center;
  color: black;
  border-radius: 10px;
  overflow: hidden;
  margin-bottom: 20px;
  animation: SlideLeft .4s;
  animation-fill-mode: forwards;
  width: 300px;
  cursor: pointer;
}

@keyframes SlideLeft {
  0% {
      margin-left: 120%;
  }

  100% {
      margin-left: 0;
  }
}

@keyframes SlideRight {
  0% {
      margin-left: 0;
  }

  100% {
      margin-left: 120%;
  }
}

.notification-item.exit {
  animation: SlideRight 0.4s;
  animation-fill-mode: forwards;
}

.notification-item p {
  margin: 0;
  padding: 10px;
}

.notification-item .bar {
  height: 10px;
  
}

.notification-item.success .bar {
  background-color: #65d266;
}

.notification-item.error .bar {
  background-color: red;
}

.notification-item.neutral .bar {
  background-color: gray;
}